<template>
  <div id="app">
    <div class="headers">
      <div class="header">
        <div class="logo">
          易解
        </div>
        <el-menu
          :default-active="activeIndex1"
          class="el-menu-demo menu"
          mode="horizontal"
          @select="handleSelect"
          background-color="#333333"
          text-color="#fff"
          active-text-color="#ffd04b" 
          >
          <a index="2"></a>
          <el-menu-item index="index">首页</el-menu-item>
          <el-menu-item index="technology" >科技易解</el-menu-item>
          <el-menu-item index="punchline" >易解热词</el-menu-item>
          <el-menu-item index="community">易解社区</el-menu-item>
          <el-menu-item index="about">关于我们</el-menu-item>
        </el-menu>
        <div class="float_header">
          <div class="header_input">
            <el-input
              placeholder="搜索一下"
              v-model="input"
              clearable size="min">
            </el-input>
          </div>
          <div class="head_img">
            <div class="img_box">
              <img src="./assets/logo.png" alt="" style="width:100%;height:100%">
            </div>
            <span style="color:#ffffff;padding-left:5px">{{user.nickName}}</span>
          </div>
        </div>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
import Bus from '../build/bus.js'
export default {
  name: 'App',
  data(){
    return{
      activeIndex1: 'index',
      input:"",
      user:[]
    }
  },
  created(){
      this.$axios.get('/user/getUser')
      .then(res=>{
        console.log(res.data);
        if(res.data.errno==0){
          this.user = res.data.data;
        }
      })
      .catch(err=>{
        console.log("错误");
      })

      Bus.$on('headerChange', (data) => {
        if(this.activeIndex1==data){
              return false;
            }else{
                this.activeIndex1 = data;
            }
        })
  },
  methods:{
          handleSelect(key, keyPath) {
            if(this.activeIndex1==key){
              return false;
            }else{
                console.log(key, keyPath);
                this.$router.push({name:key});
            }
             
          }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  padding: 0;
  margin: 0;
}
.headers{
  background: #333333;
  height: 60px;
}
.header{
  min-width: 930px;
  position: relative;
  display: flex;
  background: #333333;
  height: 60px;
  padding:0 30px;
  margin: 0 auto;
}

.logo{
  background: #333333;
  float: left; 
  width: 70px;
  font-size: 22px;
  color: white;
  line-height: 60px;
}
.el-menu--horizontal>.el-menu-item{
 height: 60px;
 line-height: 60px;
}

.menu ul {
  display: flex;
}
.header_input{
  width: 250px;
  line-height: 60px;
}
.header_input input{
  height: 30px;
  border: #333333 solid 1px;
  border-radius: 5px; 
  width: 200px;
}
.head_img{
  height: 60px;
  display: flex;
  align-items :center;
  padding-right: 30px;
}
.float_header{
  position:absolute;
  right: 0;
  display: flex;  
}
.img_box{
  width: 30px;
  height: 30px;
}
.head_img span {
  line-height: 60px;
}
</style>



